import React ,{useState,useEffect} from 'react'
import {Navbar} from './Navbar'
import { Body } from './Body'
import { Footer } from './Footer'
import { Products } from './Products'
import {auth,fs} from '../config/config'



export const Home = () => {

  // step1 
  // getting current user function that helps in finding user name 

  function GetCurrentUser(){
    // initially the use state is empty
    const [user,setUser] =useState(null)

    useEffect(() =>{
      auth.onAuthStateChanged(user =>{
        if(user)
        { // if the use exist find its full name
          fs.collection('users').doc(user.uid).get().then(snapshot =>{
            setUser(snapshot.data().FullName);
            // it sets into the user var
          })
        }

        else 
        {
          setUser(null);
        }
      })
    },[])

    // return the user into the state
    return user;
  }

  const user = GetCurrentUser();
  //console.log(user);
  return (
    <>
    {/* passing into the navbar user as props */}
    <Navbar user ={user}/>
    <Body/>
    <Products/>
    <Footer/>
    </>
  )
}
 