import React from 'react'
import {Link} from 'react-router-dom'
//import {Icon} from 'react-icons-kit'
// import {shoppingCart} from 'react-icons-kit/feather/shoppingCart'
import logo from '../images/assets/Bake_with_love.png'
import cart from '../images/assets/cart.png'
import userIcon from '../images/assets/UserIcon.png'
import {useNavigate} from 'react-router-dom';

import {auth} from '../config/config'


const sideNavigationOpen =()=>{
  document.getElementById("mySidenav").style.width = "200px";
}

const sideNavigationClose =() =>{
  document.getElementById("mySidenav").style.width = "0";
}
 


// recieving the user from home.js
export const Navbar = ({user}) => {

  // variable to check the history of status of web page
  const history = useNavigate();

  // making the button to log out
  const handleLogout =()=>{
    auth.signOut().then(() =>{
        history('/login')
    })
}

function GotoHome(){
  history('/')
}

function goToUser(){
  history('/about_user')
}

function viewAllProducts()
{
  history('/show_products')
}
function viewCart(){
  history('/cart')
}

function AboutUs()
{
  history('/contact_us')
}
  return (
    <>
    <div className="topbar">
      <div>
         <img src={logo} onClick={GotoHome}  target='pointer' type="button" alt="company logo" id ="logoimg"/>
      </div>
    </div>

    {/* <!-- logos for the account info and cart info --> */}
    
    {/* when the user is not log in it will show the buttons  */}

    {!user && <> 
      <div className="accountInfo">
      <div className='having_cart'>
      <Link to ='/login'><img src={cart} id="bag" alt="cart"/></Link>  
       <div  className='login_cart'>
       <Link to ='/login' className='login_cart'>Cart</Link>
       </div>
      </div>
      <div className='having_cart'>
      <Link to ='/login'><img src={userIcon} id="myicon" alt="my user icon"/> </Link> 
      <div className='login_cart'>
      <Link to ='/login' className='login_cart'>Login</Link>
      </div>
      </div>
      </div>
    </>}

    {/* otherwise show its full name  and a button of its cart */}

    {user && <> 

      <div className="accountInfo2">
        <div>
          {/* <div className='userName'>{user}</div> */}

          <div className="dropdown">
          <button className="dropbtn">Hi! {user}</button>
          <div className='dropdown-content'>
          {/* <Link to='/login'>About User</Link>
          <Link to='/login'>Points History</Link> */}
          <p onClick={goToUser}>About User</p>
          <p onClick={viewCart}>Your orders</p>
          <p onClick={handleLogout}>Log out</p>
          </div>
          </div>
          {/* <button type='button' className='logoutBtn' */}
          {/* onClick={handleLogout}>LOGOUT</button> */}
        </div>
        {/* <span className='cart-indicator'>{totalQty}</span> */}
        <Link to ='cart'><img src={cart} id="bag" alt="cart"/> 
        </Link> 
        {/* <span className='cart-indicate_items'>{totalProducts}</span> */}
        
     </div>
    </>}


    <div className="menuButton"  >
      <img type="button"  onClick={sideNavigationOpen} alt='sidebutton' id="menuBtn" src="https://img.icons8.com/ios-filled/40/000000/menu--v1.png" />
    </div>

    <div id="mySidenav" className="sidenav">
      <Link to="#" className="closebtn" onClick={sideNavigationClose}>&times;</Link>
      <Link to ="/show_products"><img  src="https://img.icons8.com/dotty/30/000000/birthday.png" alt='birthdays'/>  BIRTHDAY</Link>
      <Link to ="/show_products"><img src="https://img.icons8.com/plumpy/30/000000/google-shopping.png" alt='services'/>  SERVICES</Link>
      <Link to ="/show_products"><img src="https://img.icons8.com/ios/30/000000/wedding-cake.png" alt='weddings'/>  WEDDINGS</Link>
      <Link to ="/show_products"><img src="https://img.icons8.com/external-bearicons-glyph-bearicons/30/000000/external-Anniversary-time-and-date-bearicons-glyph-bearicons.png" alt='anniversary'/>ANNIVERSARY</Link>
      <Link to ="/show_products"><img src="https://img.icons8.com/wired/30/000000/paste-special.png" alt='special_days'/> SPECIAL DAYS</Link>
      <Link to ="/contact_us"><img src="https://img.icons8.com/ios/30/000000/about-us-male.png" alt='about_us'/>  ABOUT US</Link>
      <Link to ="/contact_us"><img src="https://img.icons8.com/external-others-cattaleeya-thongsriphong/30/000000/external-calling-food-delivery-outline-others-cattaleeya-thongsriphong-2.png" alt='orders'/> ORDERS</Link>

    </div> 

    {/* <!-- <span style="font-size:30px;cursor:pointer" ">&#9776;</span> --> */}

    <div className="topnav">
        <div className="top_navi">       
          <div onClick={viewAllProducts }>BIRTHDAY</div>
          <div onClick={viewAllProducts }>GRADUATION</div>
          <div onClick={viewAllProducts }>WEDDINGS</div>
          <div onClick={viewAllProducts }>ANNIVERSARY</div>
          <div onClick={viewAllProducts }>SPECIAL DAYS</div>
          <div onClick={viewAllProducts }>CHRISTMAS</div>
          <div onClick={viewAllProducts }>ORDER</div>
          <div onClick={AboutUs}>ABOUT US</div>
        </div>
    </div>

    </>
  )
}
