import React , {useState,useEffect} from 'react'
import {Navbar} from './Navbar'
import {auth,fs} from '../config/config'
import { CartProducts } from './CartProduct'
import {Footer} from './Footer'

import { useNavigate } from 'react-router-dom'
import emptycart from '../images/assets/empty_cart.png'


export const Cart = () => {
    // getting current user function
    function GetCurrentUser(){
        const [user, setUser]=useState(null);
        useEffect(()=>{
            auth.onAuthStateChanged(user=>{
                if(user){
                    fs.collection('users').doc(user.uid).get().then(snapshot=>{
                        setUser(snapshot.data().FullName);
                    })
                }
                else{
                    setUser(null);
                }
            })
        },[])
        return user;
    }

    const user = GetCurrentUser();

    // state of cart products

    const [cartProducts, setCartProducts] = useState([]);

    useEffect(()=>{
        auth.onAuthStateChanged(user=>{
            if(user){
                fs.collection('Cart ' + user.uid).onSnapshot(snapshot=>{
                    const newCartProduct = snapshot.docs.map((doc)=>({
                        ID: doc.id,
                        ...doc.data(),
                    }));
                    setCartProducts(newCartProduct);                    
                })
            }
            else{
                console.log('user is not signed in to retrieve cart');
            }
        })
    },[])

    //console.log(cartProducts)
// getting the qty from cartProducts in a seperate array
const qty = cartProducts.map(cartProduct=>{
    return cartProduct.qty;
})

// reducing the qty in a single value
const reducerOfQty = (accumulator, currentValue)=>accumulator+currentValue;

const totalQty = qty.reduce(reducerOfQty,0);

// console.log(totalQty);

// getting the TotalProductPrice from cartProducts in a seperate array
const price = cartProducts.map((cartProduct)=>{
    return cartProduct.TotalProductPrice;
})

// reducing the price in a single value
const reducerOfPrice = (accumulator,currentValue)=>accumulator+currentValue;

const totalPrice = price.reduce(reducerOfPrice,0);
    // cart prouduct increase 

const history = useNavigate();
// global variable
let Product;
    
// cart product increase function
const cartProductIncrease=(cartProduct)=>{
    // console.log(cartProduct);
    Product=cartProduct;
    Product.qty=Product.qty+1;
    Product.TotalProductPrice=Product.qty*Product.price;
    // updating in database
    auth.onAuthStateChanged(user=>{
        if(user){
            fs.collection('Cart ' + user.uid).doc(cartProduct.ID).update(Product).then(()=>{
                console.log('increment added');
            })
        }
        else{
            console.log('user is not logged in to increment');
        }
    })
}
    // cart product decrease functionality
    const cartProductDecrease =(cartProduct)=>{
        Product=cartProduct;
        if(Product.qty > 1){
            Product.qty=Product.qty-1;
            Product.TotalProductPrice=Product.qty*Product.price;
             // updating in database
            auth.onAuthStateChanged(user=>{
                if(user){
                    fs.collection('Cart ' + user.uid).doc(cartProduct.ID).update(Product).then(()=>{
                        console.log('decrement');
                    })
                }
                else{
                    console.log('user is not logged in to decrement');
                }
            })
        }
    }

// state of totalProducts
const [totalProducts, setTotalProducts]=useState(0);
// getting cart products   
useEffect(()=>{        
    auth.onAuthStateChanged(user=>{
        if(user){
            fs.collection('Cart ' + user.uid).onSnapshot(snapshot=>{
                const qty = snapshot.docs.length;
                setTotalProducts(qty);
            })
        }
    })       
},[])  
   
function BrowseProducts(){
   
    // history('/show_products')
    history('/show_products')
    // console.log('helo')

}

function GotoHome()
{
    history('/')
}

  return (
    <>
        <Navbar user={user} totalProducts={totalProducts}/>           
        <br></br>


        {cartProducts.length ===0 && (
            <div>
                <img src={emptycart} className='container img-fluid' style={{maxWidth:'100%', height:'auto'}}  alt='empty cart' />
                <div className='container m-3 text-center'  >
                <button type="button" className="btn btn-primary p-2 m-2" onClick={BrowseProducts}>Browse Products</button>
                <button type="button" className="btn btn-danger p-2 m-2" onClick={GotoHome}>Go to Home</button>
                </div>
            </div>
        )}
        {cartProducts.length > 0 && (
            <div className='container-fluid'>
                <h1 className='text-center'>Cart</h1>
                <div className='products-box'>
                    <CartProducts cartProducts={cartProducts}
                        cartProductIncrease={cartProductIncrease}
                        cartProductDecrease={cartProductDecrease}
                    />
                </div>
                <div className='summary-box'>
                    <h5>Cart Summary</h5>
                    <br></br>
                    <div>
                    Total No of Products: <span>{totalQty}</span>
                    </div>
                    <div>
                    Total Price to Pay: <span>$ {totalPrice}</span>
                    </div>
                    <br></br>
                    {/* <StripeCheckout
                    
                    ></StripeCheckout> */}
                </div>                                    
            </div>
        )}

        <Footer/>


     </>
  )
}
