import React,{useState, useEffect} from 'react'
import { Items } from './Items'
import { Navbar } from './Navbar'
import {auth,fs} from '../config/config'
import {useNavigate} from 'react-router-dom'
import {Footer} from './Footer'
import { IndividualFilteredProduct } from './IndividualFilteredProduct'

export const ShowProducts = () => {
    // getting usesr id and its name using firebase auth
    function GetUserUid(){
        const [uid, setUid]=useState(null);
        useEffect(()=>{
            auth.onAuthStateChanged(user=>{
                if(user){
                    setUid(user.uid);
                }
            })
        },[])
        return uid;
    }

  
   

    const uid = GetUserUid();

    // getting current user function
    function GetCurrentUser(){
        const [user, setUser]=useState(null);
        useEffect(()=>{
            auth.onAuthStateChanged(user=>{
                if(user){
                    fs.collection('users').doc(user.uid).get().then(snapshot=>{
                        setUser(snapshot.data().FullName);
                    })
                }
                else{
                    setUser(null);
                }
            })
        },[])
        return user;
    }


      // state of products
      const [myproducts, setMyProducts]=useState([]);

    //   // getting products function
    const getProducts = async ()=>{
          const myproducts = await fs.collection('Products').get();
          const productsArray = [];
          // getting its snapshort 
          for (var snap of myproducts.docs){
              var data = snap.data();
              data.ID = snap.id;
              productsArray.push({
                  ...data
              })
              if(productsArray.length === myproducts.docs.length){
                //setting the products
                  setMyProducts(productsArray);
              }
          }
      }
  
      useEffect(()=>{
          getProducts();
      },[])
  
      // state of totalProducts
      const [totalProducts, setTotalProducts]=useState(0);
      // getting cart products   
      useEffect(()=>{        
          auth.onAuthStateChanged(user=>{
              if(user){
                  fs.collection('Cart' + user.uid).onSnapshot(snapshot=>{
                      const qty = snapshot.docs.length;
                      setTotalProducts(qty);
                  })
              }
          })       
      },[])  
  
      // globl variable
      var Product;
  
      // add to cart
      const history = useNavigate();
      const addToCart = (myproducts)=>{
          if(uid!==null){
              // console.log(product);
              Product=myproducts;
              Product['qty']=1;
              Product['TotalProductPrice']=Product.qty*Product.price;
              fs.collection('Cart ' + uid).doc(myproducts.ID).set(Product).then(()=>{
                  console.log('successfully added to cart');
              })
  
          }
          else{
              history('/login');
          }
          
      }

    const user = GetCurrentUser();


 
   // const testmode ='hello'
    //console.log(user);
    //console.log(uid);
    // console.log(myproducts.le)

     // categories list rendering using span tag
    const [spans]=useState([
        {id: 'ElectronicDevices', text: 'Birthday'},
        {id: 'MobileAccessories', text: 'Wedding'},
        {id: 'TVAndHomeAppliances', text: 'Graduation'},
        {id: 'SportsAndOutdoors', text: 'Anniversary'},
        {id: 'HealthAndBeauty', text: 'Valentines'},
        {id: 'HomeAndLifestyle', text: 'Christmas'},
        {id: 'MensFashion', text: `Holi`},
        {id: 'WatchesBagsAndJewellery', text: `Diwali`},
        {id: 'Groceries', text: 'Mothers Day'},  
        {id: 'innugration', text:'innugration'},
        {id: 'Misc', text: 'Misc'}        
    ])

    // active class state
    const [active, setActive]=useState('');

    // category state
    const [category, setCategory]=useState('');

    // handle change ... it will set category and active states
    const handleChange=(individualSpan)=>{
        setActive(individualSpan.id);
        setCategory(individualSpan.text);
        filterFunction(individualSpan.text);
    }

    // filtered products state
    const [filteredProducts, setFilteredProducts]=useState([]);

    // filter function
    const filterFunction = (text)=>{
        if(myproducts.length>1){
            const filter=myproducts.filter((myproducts)=>myproducts.category===text);
            setFilteredProducts(filter);
        }
        else{
            console.log('no products to filter')
        } 
    }

    // return to all products
    const returntoAllProducts=()=>{
        setActive('');
        setCategory('');
        setFilteredProducts([]);
    }
    
    return (
    <>
      
        <Navbar user={user} totalProducts={totalProducts}/>           
            <br></br>   
        {/* {myproducts.length >0 && (
            <> 
            <div className='container'>
			<h3 className='text-center'>Here our top results that matches your search</h3>
		    </div>
            <div className='container-fluid'>
                <h1 className='text-center'></h1>
                <div className='products-box'>
                    <Items myproducts ={myproducts}  addToCart = {addToCart}/>
                </div>
            </div>
            </>
        )}

        {myproducts.length <1 && (

            <div className='container-fluid'>Please wait ...</div>
        )} */}

        <div className='container-fluid filter-products-main-box'>
                <div className='filter-box'>
                    <h6>Filter by category</h6>
                    {spans.map((individualSpan,index)=>(
                        <span key={index} id={individualSpan.id}
                        onClick={()=>handleChange(individualSpan)}
                        className={individualSpan.id===active ? active:'deactive'}>{individualSpan.text}</span>
                    ))}
                </div>
        {filteredProducts.length > 0&&(
            <div className='my-products'>
                <h1 className='text-center'>{category}</h1>
                <button  onClick={returntoAllProducts}>Return to All Products</button>
                <div className='products-box'>
                    {filteredProducts.map(individualFilteredProduct=>(
                        <IndividualFilteredProduct key={individualFilteredProduct.ID}
                        individualFilteredProduct={individualFilteredProduct}
                        addToCart={addToCart}/>
                    ))}
                </div>
            </div>  
        )}


        {filteredProducts.length < 1&&(
                    <>
                        {myproducts.length > 0&&(
                            <div className='my-products'>
                                <h1 className='text-center'>All Products</h1>
                                <div className='products-box'>
                                    <Items myproducts={myproducts} addToCart={addToCart}/>
                                </div>
                            </div>
                        )}
                        {myproducts.length < 1&&(
                            <div className='my-products please-wait'>Please wait...</div>
                        )}
                    </>
                )}
            </div>          
       <Footer/>
    </>
  )
}
