import React ,{useState,useEffect} from 'react'
import {auth,fs,} from '../config/config'
import { Navbar } from './Navbar';
import { Footer } from './Footer';

export const About_user = () => {

  function GetCurrentUser(){
    const [user, setUser]=useState(null);
    useEffect(()=>{
        auth.onAuthStateChanged(user=>{
            if(user){
                fs.collection('users').doc(user.uid).get().then(snapshot=>{
                    setUser(snapshot.data().FullName);
                })
            }
            else{
                setUser(null);
            }
        })
    },[])
  
  if(user)
  {
    return user.charAt(0).toUpperCase()+user.slice(1);
  }
  return user;
  
}


function GetCurrentUserEmail(){
  const [email, setUseremail]=useState(null);
  useEffect(()=>{
      auth.onAuthStateChanged(user=>{
          if(user){
              fs.collection('users').doc(user.uid).get().then(snapshot=>{
                  setUseremail(snapshot.data().Email);
              })
          }
          else{
              setUseremail(null);
          }
      })
  },[])
  return email;
}


function GetCurrentUserPassword(){
  const [passwd, setUserpasswd]=useState(null);
  useEffect(()=>{
      auth.onAuthStateChanged(user=>{
          if(user){
              fs.collection('users').doc(user.uid).get().then(snapshot=>{
                  setUserpasswd(snapshot.data().Password);
              })
          }
          else{
              setUserpasswd(null);
          }
      })
  },[])

  if(passwd)
  {
    return passwd.slice(0,5)+'XXXXXXX';
  }
  return passwd;
}
const user = GetCurrentUser();
const email = GetCurrentUserEmail();
const passwd = GetCurrentUserPassword();

 
  return (
    <>
    <Navbar user={user}/>

    <div className='container-fluid'>
    <div style={{textAlign:'center'}} ><h1>Welcome to the About User Section  </h1> </div>
    
    <div className='user_badge' style={{margin:'auto', textAlign:'center'}}>
    <img src="https://img.icons8.com/bubbles/230/000000/man-with-glasses-shopping-cart.png" alt='user badge'/>
    <img src="https://img.icons8.com/bubbles/230/000000/girl-with-glasses-shopping-cart.png" alt='women'/>
    </div>

    <div style={{margin:'auto', textAlign: 'center',fontFamily:'monospace'}}><h4>Hello {user}</h4></div>
    <div style={{margin:'auto', textAlign: 'center',fontFamily:'monospace'}}><h4>Email :{email}</h4></div>
    <div style={{margin:'auto', textAlign: 'center',fontFamily:'monospace'}}><h4>Password :{passwd}</h4></div>
    </div>
    <Footer/>
    </>
  )
}
