import React from 'react'
import CutePicCartoon from '../images/assets/pic.png'
import Bdaygirl from '../images/birthday/girl_birthday.jpg'
import gradPic from '../images/grad/grad1.jpg' 
import Wed from '../images/wedding/wed4.jpg'
import Aniversary from '../images/anniversary/anni4.jpg'
import ValentineDay from '../images/valentines/valentine1.jpg'
import Christmas from '../images/christmas/christmas2.webp'
import Social from '../images/special days/sp3.jpg'
import MakarSankranti from '../images/special days/makar_sankranti.jpg'
import Holi from '../images/special days/holi2.jpg'
import Innugration from '../images/assets/innugration.jpg'
import Diwali from '../images/special days/diwali3.jpg'
import { useNavigate } from 'react-router-dom'


 

export const Products = () => {


  const history = useNavigate()
  function viewAllProducts()
  {
    history('/show_products')
  }
  return (
    <>
    <div className="title3">
      <h3>We bake cakes for all these events</h3>
      <div className="cutepic">
        <img src={CutePicCartoon}  width="180px" alt="cartoon"/>
      </div>
     
    </div>

    <div className="overlayItems">
      <div className="mycontainer">
        <img src={Bdaygirl} width="auto"   className="imagesForOverlay" alt="bdaygirl"/>
        <div className="overlay">
          <div className="text">
            <p className='Captions' >BIRTHDAY CAKES</p>
            <button className="buttonsForView"onClick={viewAllProducts } >View More</button>
          </div>
        </div>
      </div>
  
      <div className="mycontainer">
        <img src={gradPic}  width="auto"  className="imagesForOverlay"  alt="gradpic"/>
        <div className="overlay">
          <div className="text"> 
            <p className='Captions'  >GRADUATION CAKES</p>
            <button className="buttonsForView" onClick={viewAllProducts}>View More</button>
          </div>
        </div>
      </div>
      
  
      <div className="mycontainer">
        <img src={Wed}  width="auto"  className="imagesForOverlay"   alt="wedding"/>
        <div className="overlay">
          <div className="text">
            <p className='Captions' >WEDDINGS CAKES</p>
            <button className="buttonsForView" onClick={viewAllProducts}>View More</button>
          </div>
        </div>
      </div>
      
  
      <div className="mycontainer">
        <img src={Aniversary}  width="auto" className="imagesForOverlay"    alt="anninvarsary"/>
        <div className="overlay">
          <div className="text">
            <p className='Captions' >ANNIVERSARY CAKES</p>
            <button className="buttonsForView" onClick={viewAllProducts}>View More</button>
          </div>
        </div>
      </div>
      
  
      <div className="mycontainer">
        <img src={ValentineDay} className="imagesForOverlay"  width="auto"  alt="valentines day"/>
        <div className="overlay">
          <div className="text">
            <p className='Captions' >VALENTINES CAKES</p>
            <button className="buttonsForView" onClick={viewAllProducts}>View More</button>
          </div>
        </div>
      </div>
      
  
      <div className="mycontainer">
        <img src={Christmas} className="imagesForOverlay"  width="auto"  alt="christmas"/>
        <div className="overlay">
          <div className="text">
            <p className='Captions' >CHRISTMAS CAKES</p>
            <button className="buttonsForView" onClick={viewAllProducts}>View More</button>
          </div>
        </div>
      </div>


      
      <div className="mycontainer">
        <img src={Social} className="imagesForOverlay"  width="auto"   alt="new year"/>
        <div className="overlay">
          <div className="text">
            <p className='Captions' >NEW YEAR CELEBRATIONS</p>
            <button className="buttonsForView" onClick={viewAllProducts}>View More</button>
          </div>
        </div>
      </div>


      <div className="mycontainer">
        <img src={MakarSankranti} className="imagesForOverlay"   width="auto" alt="makarsankranti"/>
        <div className="overlay">
          <div className="text">
            <p className='Captions' >MAKAR-SANKRANTI CAKES</p>
            <button className="buttonsForView" onClick={viewAllProducts}>View More</button>
          </div>
        </div>
      </div>

      <div className="mycontainer">
        <img src={Holi} className="imagesForOverlay"  width="auto" height="300px" alt="Holi"/>
        <div className="overlay">
          <div className="text">
            <p className='Captions' >GRAND HOLI CELEBRATION CAKES</p>
            <button className="buttonsForView" onClick={viewAllProducts}>View More</button>
          </div>
        </div>
      </div>
      <div className="mycontainer">
        <img src={Innugration} className="imagesForOverlay"  width="230px" height="300px" alt="innugration"/>
        <div className="overlay">
          <div className="text"> 
            <p className='Captions' >INNUGRATION - CAKES</p>
            <button className="buttonsForView" onClick={viewAllProducts}>View More</button>
          </div>
        </div>
      </div>
      

      <div className="mycontainer">
        <img src={Diwali} className="imagesForOverlay"   width="auto" height="300px" alt="Diwali"/>
        <div className="overlay">
          <div className="text">
            <p className='Captions' >DIWALI CELEBRATION CAKES</p>
            <button className="buttonsForView" onClick={viewAllProducts}>View More</button>
          </div>
        </div>
      </div>
    </div>

     </>
  )
}
