import React ,{useState,useEffect} from 'react'
import {auth,fs} from '../config/config'
import Card from 'react-bootstrap/Card';
import { useNavigate } from 'react-router-dom';
import DeveloperImg from '../images/assets/developer.png'
import { Navbar } from './Navbar';
import { Footer } from './Footer';



export const Contact_Us = () => {

    // create their states 

    const [fullName , setFullname]=useState('');
    const [email,setEmail]  = useState('')
    const [errorMsg, setErrorMsg]=useState('');
    const [successMsg, setSuccessMsg]=useState('');
    const [subject ,setSubject] = useState('');
    const [message, setMessage] = useState('');

    const history = useNavigate();
    const handleContactForm =(e)=>{
        e.preventDefault();

        fs.collection('ContactInfo').doc(user.uid).set({
            FullName:fullName,
            Email: email,
            Subject: subject,
            Message: message
        }).then(()=>{
            setSuccessMsg('Form Submitted Successfully');
            setFullname('');
            setEmail('');
            setSubject('');
            setMessage('');
            setErrorMsg('');

            setTimeout(()=>{
                setSuccessMsg('');
                history('/');
            },2000)
        }).catch(error =>setErrorMsg(error.message));
    }

    function GetCurrentUser(){
        // initially the use state is empty
        const [user,setUser] =useState(null)
    
        useEffect(() =>{
          auth.onAuthStateChanged(user =>{
            if(user)
            { // if the use exist find its full name
              fs.collection('users').doc(user.uid).get().then(snapshot =>{
                setUser(snapshot.data().FullName);
                // it sets into the user var
              })
            }
    
            else 
            {
              setUser(null);
            }
          })
        },[])
    
        // return the user into the state
        return user;
      }
    
      const user = GetCurrentUser();
  return (
    
<>
<Navbar user ={user}/>
<section>
    <h2 className="h1-responsive font-weight-bold text-center my-4">Contact us</h2>
 
    <p className="text-center w-responsive mx-auto mb-5">Do you have any questions? Please do not hesitate to contact us directly. Our team will come back to you within
    a matter of hours to help you.</p>
    {successMsg && <>
                <div className='success-msg'>{successMsg}</div>
            </>}
    <div className="row">
    <div className="col-md-5 mb-md-3 mb-5" style={{margin:'auto'}}>
        <form id="contact-form" name="contact-form" onSubmit={handleContactForm}>     
            <div className="row">
                <div className="col-md-6">
                    <div className="md-form mb-0">
                        <label for="name" className="">Your name</label>
                        <input type="text" id="name" name="name" className="form-control" onChange={(e) =>setFullname(e.target.value)} value={fullName}/>
                    </div>
                </div>
     
                <div className="col-md-6">
                    <div className="md-form mb-0">
                        <label for="email" className="">Your email</label>
                        <input type="text" id="email" name="email" className="form-control" onChange={(e)=>setEmail(e.target.value)} value={email}/>
                    </div>
                </div>
            </div>
         
            <div className="row">
                <div className="col-md-12">
                    <div className="md-form mb-0">
                        <label for="subject" className="">Subject</label>
                        <input type="text" id="subject" name="subject" className="form-control" onChange={(e)=>setSubject(e.target.value)} value={subject}/>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-md-12">

                    <div className="md-form">
                        <label for="message">Your message</label>
                        <textarea type="text" id="message" name="message" rows="2" className="form-control md-textarea" onChange={(e)=>setMessage(e.target.value)} value={message}></textarea>
                    </div>
                </div>
            </div>
            <div className="text-center text-md-left">
                <button className="btn btn-primary mt-3"  type='submit'  style={{margintop:'10px'}}>Send</button>
            </div>
        </form> 

        {errorMsg && <>
            <div className='error-msg'>{errorMsg}</div>
        </>
        }     
    </div>
 
    <div className="col-md-5 text-center">
        <ul className="list-unstyled mb-0">
            <li>  <img src="https://img.icons8.com/stickers/50/000000/shop-location.png" alt='location'/>
                <p>Jyoti Nagar New Development,Tinsukia Asssam 786125, India</p>
            </li>

            <li> <img src="https://img.icons8.com/color/50/000000/apple-phone.png" alt='phone'/>
                <p>+91 7002936170</p>
            </li>

            <li> <img src="https://img.icons8.com/external-nawicon-flat-nawicon/50/000000/external-email-communication-nawicon-flat-nawicon-2.png" alt='emails'/>
                <p>pursottamsah@outlook.com</p>
            </li>
        </ul>
    </div>
 

</div>

</section>


 
<div className='container ml-4' style={{margin:'auto', textAlign:'center'}}>

<Card style={{ width: '18rem',margin:'auto' }}>
      <Card.Img variant="top" src={DeveloperImg} />
      <Card.Body>
        <Card.Title>Pursottam Sah</Card.Title>
        <Card.Text>
           Student at NIT Arunachal Pradesh CSE 3rd Year. We are greatly thankful to him for creating such a beautiful Website
        </Card.Text>

        <a href="https://github.com/Pursottam6003"  rel="noreferrer" target="_blank"><img src="https://img.icons8.com/ios-glyphs/48/000000/github.png" alt='github'/></a>
        <a href="https://www.linkedin.com/in/pursottam-sah-78aa4a1bb/" rel="noreferrer" target="_blank"><img src="https://img.icons8.com/color/48/000000/linkedin.png" alt='linkedin'/></a>
        <a href="https://www.instagram.com/rahulsah6003/" rel="noreferrer" target="_blank"><img src="https://img.icons8.com/color/48/000000/instagram-new--v1.png" alt='instagram'/></a>
      </Card.Body>
</Card>

</div>

<br></br>

<Footer/>
</>     
  )
}
